@import url(https://cdnjs.cloudflare.com/ajax/libs/minireset.css/0.0.2/minireset.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.calculator{display:grid;grid-template-rows:2fr 1fr 1fr 1fr 1fr 1fr;grid-template-columns:repeat(4, 1fr);grid-gap:10px;width:190px;height:400px;margin:50px auto}.calculator__screen{grid-column-start:1;grid-column-end:5;display:flex;flex-direction:column;align-items:flex-end;justify-content:center;padding:0 10px;border:1px solid #aeaeae;border-radius:2px;background-color:rgba(204,204,204,0.2)}.calculator__field{display:flex;align-items:center;justify-content:center;width:40px;height:50px;background-color:rgba(204,204,204,0.2);border:1px solid #aeaeae;border-radius:2px}.calculator__expression{font-size:20px}

.App{text-align:center}

